.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about__me{
    width: 80%;
    height: 80%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
        transparent,
        transparent
    );
    display: grid;
    place-items: center;

}

.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-image:hover{
    transform: rotate(0);
}

.about__cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
}

.about__card{
    border-radius: 1rem;
    text-align: center;
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    padding: 2rem;
    transition: var(--transition);
}

.about__card:hover{
    border-color: var(--color-primary-variant);
    background: transparent;
    cursor: default;
}

.about__icon{
    color: var(--color-primary);
    font-size: 1.6rem;
    margin-bottom: 1rem;
}

.about__card h5{
    font-size: 1rem;
}

.about__card small{
    font-size: 0.8rem;
    color: var(--color-light);
}

.about__content p{
    margin: 2rem 0 2.7rem;
    color: var(--color-light);
}

/* Responsiveness Queries */

@media screen and (max-width: 1024px){
    .about__container{
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about__me{
        width: 40%;
        margin: 1rem auto 8rem;
    }

    .about__content p{
        margin: 1rem 0 1.5rem;
    }
    
}


@media screen and (max-width: 600px){
    .about__me{
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__content{
        text-align: center;
    }

    .about__content p{
        margin: 1.5rem 0;
    }


}